.footer-hover-underline-animation {
   display: inline-block;
   position: relative;
   color: #B9B7B7;
   cursor: pointer;
}
.footer-hover-underline-animation:after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #B9B7B7;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.footer-hover-underline-animation:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}
.breadcrumbs-hover-underline-animation {
   display: inline-block;
   position: relative;
   color: #707070;
   cursor: pointer;
}
.breadcrumbs-hover-underline-animation:after {
  content: '';
  position: absolute;
  width: 100%;
  transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #707070;
  transform-origin: bottom right;
  transition: transform 0.25s ease-out;
}
.breadcrumbs-hover-underline-animation:hover:after {
  transform: scaleX(1);
  transform-origin: bottom left;
}
